<template>
  <section>
     <div class="top">
       <span class="sch"><img src="../assets/search.png" height="16" width="16"/></span>
       <input class="ipt" v-model="value" type="text">
       <span class="cancel"><img v-if="value" v-on:click="cancel" src="../assets/cross.png" height="16" width="16"/></span>
     </div>
    <div class="show" v-if="value" v-on:click="addHistory">搜索"{{value}}"</div>
    <div class="rec" v-on:click = 'submit'>
      <span v-for="(item,key) in recommend" :key="key" v-if="!value">{{item}}</span>
    </div>
    <ul class="history" v-if="!value">
      <li v-for="item in history">
        <span class="clk">  <img src="../assets/clock.png" height="16" width="16"/></span>
        <span class="cnt" v-on:click="addSch"> {{item}}  </span>
        <span  class="cross" v-on:click="delHistory" :data-item="item">  <img src="../assets/x.png" height="16" width="16"/></span>
      </li>
    </ul>
  </section>
</template>
<script>
  import axios from 'axios'
  export default {
      name:'search',
      data(){
          return{
              value:'',
              recommend:[],
              history:[]
          }
      },
      beforeMount:function () {
        let _this = this;
        axios({
          method:'post',
          url:'/recommend'
        })
          .then(function (res) {
              console.log(res);
            _this.recommend = res.data.data
          })
      },
      methods:{
          cancel:function () {
            this.value='';
          },
        submit:function (e) {
          if (e.target === e.currentTarget){
              return false;
          }else {
              this.value = e.target.innerText;
          }
        },
        addHistory:function () {
          //数组包含
            let _this = this;
            function inArray(num) {
              let pos = -1;
              _this.history.map(function (item,index) {
                  if(item===num){
                      pos=index;
                  }
              });
              return pos;
            }
            if (inArray(this.value)<0){
                this.history.push(this.value)
            }
        },
        delHistory:function (e) {
              let value = e.currentTarget.getAttribute("data-item");
              let temp = -1;
              this.history.map(function (item,index) {
                if(item===value){
                    temp=index
                }
              });
              this.history.splice(temp,1)
        },
        addSch:function (e) {
          this.value = e.target.innerText;
        }
      }
  }
</script>
<style lang="less" scoped>
   .top {
     width: 90vw;
     height: 4vh;
     margin: 2vh auto;
     display: flex;
     justify-content: space-around;
     align-items: center;
     padding: 4px auto;
     border-radius: 2vh;
     background-color: #ebecec;
     .sch{
       width: 4vw;
     }
     .ipt {
       width: 78vw;
       height: 4vh;
       line-height: 4vh;
       padding: 0;
       margin: 0;
       border: none;
       background-color: transparent;
       outline: none;
     }
     .cancel {
       width: 6vw;
     }
   }
  .show {
    height: 4vh;
    padding: 2vh 5vw;
    line-height: 4vh;
    text-align:left;
    border-bottom: 1px solid lightgrey;
    border-top: 1px solid lightgrey;
  }
  .rec {
    width: 90vw;
    margin: 4vh auto;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    span {
      display: inline-block;
      border: 1px solid grey;
      padding: 2vw;
      height: 2vw;
      margin: 2vw;
      line-height: 2vw;
      border-radius: 3vw;
    }
  }
  .history {

    li {
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 100vw;
      text-align: left;
      height: 6vh;
      line-height: 6vh;

      .clk {
        padding-left: 1vw;
        width: 5vw;
        margin: auto;
        img {
          margin-top: 3vw;
        }
      }
      .cnt {
        width: 82vw;
        border-bottom: 1px solid lightgrey;
      }
      .cross{
        width: 5vw;
        padding-right: 4vw;
        border-bottom: 1px solid lightgrey;
      }
    }
  }
</style>



