import Vue from 'vue'
import Router from 'vue-router'
import Recommend from '@/components/Recommend'
import Hot from  '@/components/Hot'
import Search from  '@/components/Search'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Recommend',
      component: Recommend
    },
    {
      path:'/Hot',
      name:'Hot',
      component:Hot
    },
    {
      path:'/search',
      name:'Search',
      component:Search
    }
  ]
})



