<template>
  <ul class="list" v-on:click="sel">
      <li   v-bind:class="{isactive:active===1}"> <router-link  to="/" class="name" id="first">推荐音乐</router-link></li>
      <li  v-bind:class="{isactive:active===2}"><router-link to="/Hot"  class="name" id="second">热歌榜</router-link></li>
      <li  v-bind:class="{isactive:active===3}"><router-link to="/search" class="name"  id="third">搜索</router-link></li>
  </ul>
</template>
<script>
  export default {
      name:'nav-link',

    data () {
      return {
        active: 1
      }
    },
      methods:{
        sel:function (e) {
            console.log(e.target.id);
          switch(e.target.id){
            case('first'):
                console.log('ha');
                this.active =1;
                break;
            case ('second'):
                this.active = 2;
              break;
            case  ('third'):
                this.active = 3;
              break;
            default:
                this.active =1

          }
        }
      }
  }
</script>
<style lang="less" scoped>
  .isactive {
    border-bottom:1px solid red;
    color: red;
  }
   .list {
     display: flex;
     justify-content: space-around;
     text-align: center;
     height: 6vh;
     line-height: 6vh;
     border-bottom: 1px solid lightgrey;
     li {
       list-style-type: none;
       width: 33.3vw;
       .name{
         display: inline-block;
         width: 100%;
         height: 100%;
       }
     }
   }
   //覆盖link原有的激活的样式
  .router-link-active {
    text-decoration: none;
    color: black;
  }
</style>



