<template>
  <section>
    <h3 class="tt">推荐歌单</h3>
    <ul class="list1">
     <li v-for="item in list" class='blc'>
       <img class="list-img" :src='item.pic' alt="#">
       <p>{{item.intro}}</p>
       <div class="listener"><img src="../assets/ear.png" height="20" width="20"/> <span>{{item.listeners}}</span></div>
     </li>
    </ul>
  </section>
</template>
<script>
  import axios from 'axios';
  export default {
      name:'list',
      data(){
        return{
            list:{}
        }
      },
      beforeMount:function (e) {
        let _this = this;
        axios({
          method: 'post',
          url: '/list',
        })
          .then(function (res) {
            _this.list = res.data.data;
            console.log(_this.list)

          })
          .catch(function (err) {
            console.log(err)
          })
        ;
      },
      methods:{
          cross:function (e) {
            axios({
              method: 'post',
              url: '/list',
            })
              .then(function (res) {
                console.log(res.data.message)
              })
              .catch(function (err) {
                console.log(err)
              })
            ;
          }
      }
  }
</script>
<style lang="less" scoped>
    .tt {
      border-left: 1px solid red;
      padding-left: 9px;
      text-align: left;
      font-weight: 400;
    }
    .list1 {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      .blc {
        position: relative;
        display: inline-block;
        width: 32vw;
        .list-img {
          width: 32vw;
          height: 32vw;
        }
        p {
          font-size:12px ;
          text-align: left;
          margin: 4px auto 10px auto;
        }
        .listener {
          position: absolute;
          top:0;
          right: 0;
          display: flex;
          align-items: center;
          color: white;
          font-size: 10px;
        }
      }
    }

</style>



