<template>
  <section>
    <div class="banner">
      <p class="tt"><img src="../assets/music.png" height="25" width="25"/>热歌榜单</p>
    </div>
    <ul class="songs">
      <li class="song" v-for="(item,index) in hotSongs">
        <div :class="{hot:index<3}" class="num">{{(index+1+'').toString().length>1?index+1:'0'+(index+1)}}</div>
        <div class="cnt">
          <p class="name">{{item.name}}</p>
          <p class="intro">{{item.author}}-{{item.album}}</p>
        </div>
        <div class="play"><img src="../assets/play.png" height="32" width="32"/></div>
      </li>
    </ul>
  </section>
</template>
<script>
  import axios from 'axios'
  export default {
      name:'hot',
      data(){
          return{
              hotSongs:[]
          }
      },
      beforeMount:function () {
        let _this = this;
        axios({
          method:'post',
          url:'/hotSongs'
        })
          .then(function (res) {
          _this.hotSongs = res.data.data;
        })
      },
  }
</script>
<style lang="less" scoped="">
.banner {
  position: relative;
  width: 100vw;
  height: 22vh;
  background: url(../assets/banner.png) center center no-repeat;
  background-size: cover;
  .tt{
    position: absolute;
    top:0;
    bottom: 0;
    left:0;
    right: 0;
    height: 2rem;
    line-height: 2rem;
    color: white;
    margin:auto;
    font-size: 1.8rem;
    text-align: center;
  }
}
  .songs {
    margin-top: 2vh;
    .song {
      display: flex;
      justify-content: space-around;
      flex-wrap:wrap ;
      align-items: center;
      width: 100vw;
      height: 7vh;
      .num {
        width: 5vw;
        padding:0 2vw;
      }
      .cnt {
        width: 81vw;
        text-align: left;
        border-bottom: 1px solid lightgray;
        .name {
          height: 3.5vh;
          line-height: 3.5vh;
          font-size: 1.2rem;
        }
        .intro {
          height: 3.5vh;
          line-height: 3.5vh;
          font-size: 0.8rem;
          white-space: nowrap;
          overflow: hidden;
          color: #888;
        }
      }
      .play {
        padding-right: 5vw;
        width: 5vw;
        height: 7vh;
        border-bottom: 1px solid lightgray;
        img{
          margin-top: 2vw;
        }
      }
    }
  }
  .hot {
    color: red;
  }
</style>



