<template>
  <section>
    <h3 class="tt">最新音乐</h3>
    <ul class="songs">
      <li v-for="item in songs" class='item'>
        <div class="lft">
          <p class="name">{{item.name}}</p>
          <p class="exp">{{item.author}}-{{item.album}}</p>
        </div>
        <div class="rt"><img src="../assets/play.png" height="28" width="28"/></div>
      </li>
    </ul>
  </section>
</template>
<script>
  import axios from 'axios';
  export default {
    name:'List',
    data(){
      return{
        songs:[]
      }
    },
    beforeMount:function (e) {
      let _this = this;
      axios({
        method: 'post',
        url: '/songs',
      })
        .then(function (res) {
          _this.songs = res.data.data;
          console.log(_this.songs)
        })
        .catch(function (err) {
          console.log(err)
        })
      ;
    },
    methods:{
      cross:function () {
      }
    }
  }
</script>
<style lang="less" scoped>
  .tt {
    border-left: 1px solid red;
    padding-left: 9px;
    text-align: left;
    font-weight: 400;
  }
  .songs {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    width: 100vw;
    height: 6vh;
    text-align: left;
    .item {
      .lft {
        display: inline-block;
        width: 88vw;
        height: 7vh;
        .name {
          height: 3.5vh;
          line-height: 3.5vh;
          font-size: 16px;
        }
        .exp {
          height: 3.5vh;
          line-height: 3.5vh;
          font-size: 12px;
          color: dimgrey;
        }
      }
      .rt {
        display: inline-block;
        width: 8vw;
        height: 7vh;
      }
    }

  }

</style>



